@import url(https://fonts.googleapis.com/css?family=Roboto);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body {
    height: 100%;
    font-family: 'Roboto', sans-serif;
    background: -webkit-gradient(linear, right top, left top, from(#79CB0A), to(#B5D020));
    background: linear-gradient(to left, #79CB0A, #B5D020);
}
.container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
header {
    margin: 30px 0;
    text-align: center;
}
header h2 {
    text-transform: uppercase;
    margin: 0;
}
header p {
    font-size: 1em;
}
header a {
    text-decoration: none;
    border-bottom: 2px dotted #FF8700;
    color: inherit;
}
.signup {
    width: 700px;
    height: 460px;
    position: relative;
    overflow: hidden;
    -webkit-box-shadow: 10px 10px 60px rgba(0, 0, 0, 0.4);
            box-shadow: 10px 10px 60px rgba(0, 0, 0, 0.4);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-radius: 5px;
}
.signup:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    z-index: -1;
    background: url(https://source.unsplash.com/Qmox1MkYDnY/900x500) no-repeat;
}
form {
    width: 50%;
    height: 80%;
    padding: 20px 50px 20px 20px;
}
form .form-group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin: 0 0 20px 0;
    color: #8F949B;
    font-size: .9em;
}
form .form-group label {
    margin: 0 0 16px 0;
}
form .form-group span {
    font-size: .8em;
    color: #FF3400;
}
form .form-group span#characters {
    font-size: .8em;
    color: #8F949B;
}
.form-group input[type="text"],
.form-group input[type="password"] {
    border: none;
    outline: none;
    background: transparent;
    padding: 0 0 10px 0;
    font-size: 1em;
    border-bottom: 1px dotted #8F949B;
}
.form-group input[type="password"] {
    color: #FF3400;
}
form .form-group:last-child {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 40px 0 0 0;
}
form .form-group:last-child button {
    border: none;
    background: -webkit-gradient(linear, right top, left top, from(#FF8700), to(#FFB100));
    background: linear-gradient(to left, #FF8700, #FFB100);
    padding: 10px 30px;
    border-radius: 20px;
    color: #FFFFFF;
    font-size: .8em;
    font-weight: bold;
    letter-spacing: 1px;
    cursor: pointer;
}
form .form-group:last-child button.active {
    opacity: .5;
    cursor: default;
}
form .form-group:last-child a {
    color: #8F949B;
    -webkit-text-decoration: dotted;
            text-decoration: dotted;
    font-size: .8em;
}
